import { useEffect } from 'react';
import { PeriodosDiv } from "./style";
import PageHeader from '../../components/PageHeader';

export default function Periodos() {

/*  Muda o título da página só na primeira renderização  */
    useEffect(() => {
        document.title = 'Periodos';
    }, []);

    return (
        <PeriodosDiv>
            <PageHeader
            title='Primeiro'/>
        </PeriodosDiv>
    );
};